.header {
   position: fixed;
   z-index: 100;
   top: 0;
   width: 100vw;
   left: 0;
   right: 0;
   transition: $transition;

   &.light {
      background-color: $romance;
      .header {
         &__burger {
            span,
            &::before,
            &::after {
               background-color: $black;
            }
         }
         &__logo {
            path {
               fill: $blue-stone;
            }
         }
         &__links a {
            color: $black;
         }
         &__icons svg path {
            stroke: $black;
         }
      }
   }
   &.dark {
      background-color: $black;
      .header {
         &__burger {
            span,
            &::before,
            &::after {
               background-color: $romance;
            }
         }
         &__logo {
            path {
               fill: $romance;
            }
         }
         &__links a {
            color: $romance;
         }
         &__icons svg path {
            stroke: $romance;
         }
      }
   }
   // .header__wrapper

   &__wrapper {
      height: var(--header);
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
   }

   // .header__left

   &__left {
      display: flex;
      align-items: center;
      gap: 36px;
      @media (max-width: $tablet) {
         gap: 0;
      }
      @media (max-width: $mobile) {
         gap: 16px;
      }
   }

   // .header__burger

   &__burger {
      user-select: none;
      width: 24px;
      height: 24px;
      cursor: pointer;
      position: relative;
      span,
      &::before,
      &::after {
         content: "";
         position: absolute;
         height: 1px;
         width: 18px;
         background-color: $romance;
         transition: $transition;
      }
      span {
         transform-origin: center;

         top: 50%;
         left: 50%;
         translate: -50% -50%;
      }
      &::before {
         top: 5px;
         left: 50%;
         translate: -50% 0;
      }
      &::after {
         bottom: 5px;
         left: 50%;
         translate: -50% 0;
      }
      &.active {
         span {
            scale: 0;
         }
         &::before {
            rotate: 45deg;
            translate: 4% 9px;
            top: 3px;
            left: 10%;
         }
         &::after {
            bottom: 7px;
            left: 10%;
            rotate: -45deg;
            translate: 4% -4px;
         }
      }
   }

   // .header__logo

   &__logo {
      position: absolute;
      top: 50%;
      left: 50%;
      translate: -50% -50%;
      path {
         transition: $transition;
      }
      @media (max-width: $tablet) {
         position: relative;
         top: unset;
         left: unset;
         translate: unset;
         margin-left: 30px;
         margin-right: 64px;
      }
      @media (max-width: 750px) {
         margin-left: 12px;
         margin-right: 24px;
      }
      @media (max-width: $mobile) {
         margin: 0;
         position: absolute;
         top: 50%;
         left: 50%;
         translate: -50% -50%;
      }
   }

   // .header__links

   &__links {
      display: flex;
      align-items: center;
      gap: 32px;
      & > li > a {
         color: $romance;
         transition: $transition;

         @include body-2;
         @media (max-width: $tablet) {
            @include body-2-tablet;
         }
         position: relative;
         border-bottom: 1px solid transparent;
         @media (hover: hover) {
            &:hover {
               border-color: $romance;
            }
         }
      }
      @media (max-width: 750px) {
         gap: 24px;
      }
      @media (max-width: $mobile) {
         display: none;
      }
   }

   // .header__icons

   &__icons {
      display: flex;
      align-items: center;
      gap: 24px;
      a {
         height: 20px;
         width: 20px;
         svg path {
            transition: $transition;
         }
      }
      @media (max-width: $tablet) {
         gap: 20px;
      }
      @media (max-width: 750px) {
         gap: 10px;
      }
      @media (max-width: $mobile) {
         gap: 16px;
         li:first-child {
            display: none;
         }
      }
   }
}
.header-search {
   display: none;
   @media (max-width: $mobile) {
      display: flex;
   }
}
.header-menu {
   background-color: $black;
   position: fixed;
   top: var(--header);
   left: 0;
   width: 100vw;
   padding-top: 40px;
   padding-bottom: 60px;
   padding-left: 58px;
   overflow: hidden;
   opacity: 0;
   pointer-events: none;
   transition: $transition;
   &.active {
      opacity: 1;
      pointer-events: all;
   }
   @media (max-width: $tablet) {
      padding-top: 30px;
      padding-left: 0;
   }
   @media (max-width: $mobile) {
      padding-bottom: 0;
      overflow: auto;
   }
   // .header-menu__wrapper
   &__mains {
      display: none;
      @media (max-width: $mobile) {
         display: flex;
         flex-direction: column;
         gap: 32px;
         & > li {
            &:has(.active) {
               & > button {
                  pointer-events: none;
               }
            }
            & > button {
               width: 100%;
               display: flex;
               align-items: center;
               justify-content: space-between;

               & > span {
                  @include body-1-mobile;
                  color: $romance;
               }
            }
         }
      }
   }
   &__wrapper {
      display: flex;
      @media (max-width: $mobile) {
         flex-direction: column;
         gap: 60px;
         height: calc(100dvh - var(--header));
         overflow-y: auto;
         margin-left: calc(-1 * var(--container));
         margin-right: calc(-1 * var(--container));
         padding-left: calc(1 * var(--container));
         padding-right: calc(1 * var(--container));
         width: 100vw;
      }
   }

   // .header-menu__main

   &__main {
      flex: 0 0 419px;
      padding-right: 155px;
      position: relative;
      &::after {
         content: "";
         display: block;
         width: 1px;
         height: 100%;
         position: absolute;
         left: 100%;
         top: 0;
         background: radial-gradient(
            3028.31% 111.8% at 0% 0%,
            rgba(248, 244, 232, 0) 0%,
            rgba(248, 244, 232, 0.25) 26.69%,
            #f8f4e8 50.34%,
            rgba(248, 244, 232, 0.25) 73.95%,
            rgba(248, 244, 232, 0) 100%
         );
      }
      @media (max-width: $tablet) {
         flex: unset;
         padding-right: min(17vw, 130px);
      }
      @media (max-width: $mobile) {
         padding: 0;
         flex: 1;
         &::after {
            display: none;
         }
      }
   }

   // .header-menu__title

   &__title {
      color: $grey-dark;
      @include body-4;
      @media (max-width: $tablet) {
         @include body-3-tablet;
      }
      @media (max-width: $mobile) {
         display: none;
      }
   }

   // .header-menu__links

   &__links {
      display: flex;
      flex-direction: column;
      gap: 32px;
      a {
         @include h5;
         color: $romance;
         @media (max-width: $tablet) {
            @include body-2-tablet;
         }
         @media (max-width: $mobile) {
            @include body-4-mobile;
         }
      }
   }

   // .header-menu__right

   &__right {
      padding-left: 155px;
      display: flex;
      flex-direction: column;
      gap: 75px;
      @media (max-width: $tablet) {
         padding-left: min(13vw, 100px);
         gap: 63px;
      }
      @media (max-width: $mobile) {
         padding: 0;
         gap: 24px;
         flex-direction: row;
         justify-content: space-between;
         flex-wrap: wrap;
         background-color: $blue-stone;
         padding-bottom: 54px;
         margin-left: calc(-1 * var(--container));
         margin-right: calc(-1 * var(--container));
         padding-left: calc(1 * var(--container));
         padding-right: calc(1 * var(--container));
         width: 100vw;
         padding-top: 33px;
      }
   }

   // .header-menu__block

   &__block {
      display: flex;
      flex-direction: column;
      gap: 32px;
   }

   // .header-menu__contacts

   &__contacts {
      display: flex;
      flex-direction: column;
      gap: 24px;
      a {
         @include h5;
         color: $romance;
         @media (max-width: $tablet) {
            @include body-2-tablet;
         }
         @media (max-width: $mobile) {
            @include body-6-mobile;
         }
      }
      li {
         @include body-7;
         color: $grey;
         @media (max-width: $tablet) {
            @include body-4-tablet;
         }
         @media (max-width: $mobile) {
            @include body-6-mobile;
         }
      }
   }

   // .header-menu__social

   &__social {
      .footer__icons {
         margin: 0;
         a {
            @media (max-width: $mobile) {
               width: 45px;
               height: 45px;
               flex: 0 0 45px;
               border: 1px solid rgba(248, 244, 232, 0.2) !important;
            }
         }
      }
      & > p {
         margin-top: -12px;
         font-size: 9px !important;
      }
      @media (max-width: $mobile) {
         margin-top: 0 !important;
      }
   }
}
.header-assort {
   position: fixed;
   background-color: $black;
   position: fixed;
   top: var(--header);
   left: 0;
   width: 100vw;
   padding-top: 40px;
   padding-bottom: 60px;
   padding-left: calc(58px + var(--containerFull));
   overflow: hidden;
   opacity: 0;
   pointer-events: none;
   transition: $transition;
   display: flex;
   &__links {
      flex: 0 0 560px;
      display: flex;
      flex-direction: column;
      gap: 40px;
      margin-right: 40px;
      a {
         @include body-4;
         color: $romance;
      }
      position: relative;
      &::after {
         content: "";
         display: block;
         width: 1px;
         height: 100%;
         position: absolute;
         left: 100%;
         top: 0;
         background: radial-gradient(
            3028.31% 111.8% at 0% 0%,
            rgba(248, 244, 232, 0) 0%,
            rgba(248, 244, 232, 0.25) 26.69%,
            #f8f4e8 50.34%,
            rgba(248, 244, 232, 0.25) 73.95%,
            rgba(248, 244, 232, 0) 100%
         );
         @media (max-width: $tablet) {
            display: none;
         }
      }
   }
   &__image {
      border-radius: 10px;
      overflow: hidden;
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
      @media (max-width: $tablet) {
         display: none;
      }
   }
   @media (max-width: $mobile) {
      left: 100vw;
      opacity: 1;
      pointer-events: none;
      display: flex;
      flex-direction: column;
      gap: 46px;
      padding-left: var(--container);
      padding-top: 34px;
      height: calc(100dvh - var(--header));
      overflow: auto;
      &__up {
         @include body-1-mobile;
         color: $romance;
         display: flex;
         align-items: center;
         justify-content: center;
         text-align: center;
         position: relative;
         button {
            position: absolute;
            top: 50%;
            left: 0;
            translate: 0 -50%;
         }
      }
      &__links {
         gap: 40px;
         flex: unset;
         a {
            @include body-4-mobile;
            color: $romance;
         }
      }
      &.active {
         left: 0;
         pointer-events: all;
         z-index: 2;
      }
   }
}
.header {
   &:has([data-assort]:hover),
   &:has([data-collection]:hover) {
      background-color: $black;
      .header {
         &__burger {
            span,
            &::before,
            &::after {
               background-color: $romance;
            }
         }
         &__logo {
            path {
               fill: $romance;
            }
         }
         &__links a {
            color: $romance;
         }
         &__icons svg path {
            stroke: $romance;
         }
      }
   }
}
@media (min-width: 569px) {
   [data-assort]:hover {
      .header-assort {
         opacity: 1;
         pointer-events: all;
      }
   }
   [data-assort] {
      position: relative;
      &::after {
         content: "";
         position: absolute;
         top: 100%;
         left: 0;
         display: block;
         height: 30px;
         width: 100%;
         cursor: pointer;
      }
   }
   [data-collection]:hover {
      .header-collection {
         opacity: 1;
         pointer-events: all;
      }
   }
   [data-collection] {
      position: relative;
      &::after {
         content: "";
         position: absolute;
         top: 100%;
         left: 0;
         display: block;
         height: 30px;
         width: 100%;
         cursor: pointer;
      }
   }
}

.header-collection {
   position: fixed;
   background-color: $black;
   position: fixed;
   top: var(--header);
   left: 0;
   width: 100vw;
   padding-top: 40px;
   padding-bottom: 60px;
   padding-left: calc(58px + var(--containerFull));
   overflow: hidden;
   opacity: 0;
   pointer-events: none;
   transition: $transition;
   display: flex;
   @media (max-width: $tablet) {
      padding-left: calc(24px + var(--containerFull));
   }
   @media (max-width: $mobile) {
      left: 100vw;
      opacity: 1;
      pointer-events: none;
      padding-left: var(--container);
      padding-top: 34px;
      height: calc(100dvh - var(--header));
      overflow: auto;

      display: flex;
      flex-direction: column;
      gap: 0;
      &.active {
         left: 0;
         pointer-events: all;
         z-index: 2;
      }
   }
   // .header-collection__items
   &__up {
      display: flex;
      align-content: end;
      justify-content: center;
      text-align: center;
      position: relative;
      @include body-1-mobile;
      color: $romance;
      button {
         position: absolute;
         top: 50%;
         left: 0;
         translate: 0 -50%;
      }
   }
   &__items {
      display: flex;
      flex-direction: column;
      gap: 40px;
      flex: 0 0 min(40vw, 574px);
      position: relative;
      &::after {
         content: "";
         display: block;
         width: 1px;
         height: 100%;
         position: absolute;
         left: 100%;
         top: 0;
         background: radial-gradient(
            3028.31% 111.8% at 0% 0%,
            rgba(248, 244, 232, 0) 0%,
            rgba(248, 244, 232, 0.25) 26.69%,
            #f8f4e8 50.34%,
            rgba(248, 244, 232, 0.25) 73.95%,
            rgba(248, 244, 232, 0) 100%
         );
         @media (max-width: $tablet) {
            display: none;
         }
      }
      @media (max-width: $tablet) {
         flex: 0 0 390px;
      }
      @media (max-width: $mobile) {
         flex: unset;
         margin-top: 46px;
         margin-bottom: 60px;
      }
   }

   // .header-collection__list

   &__list {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 18px;
      row-gap: 40px;
      a {
         display: flex;
         align-items: center;
         gap: 16px;
         @include body-4;
         color: $romance;
         .circle {
            --size: 35px;
            img {
               width: 100%;
               height: 100%;
               object-fit: cover;
            }
         }
      }
      @media (max-width: $tablet) {
         column-gap: 150px;
         a {
            @include body-4-mobile;
            .circle {
               --size: 29px;
            }
         }
      }
      @media (max-width: $mobile) {
         column-gap: 75px;
         padding-right: 12px;
      }
   }

   // .header-collection__link

   &__link {
      display: flex;
      align-items: center;
      gap: 12px;
      @include body-6;
      color: $romance;
      .circle {
         --size: 32px;
         border: 1px solid $romance;
      }
   }

   // .header-collection__gallery

   &__gallery {
      padding-right: 12px;
      padding-left: min(10vw, 143px);
      display: flex;
      flex-direction: column;
      gap: 20px;
      text-align: center;
      img {
         width: 100%;
         border-radius: 10px;
         object-fit: cover;
      }
      @media (max-width: $tablet) {
         display: none;
      }
   }

   // .header-collection__label

   &__label {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      @include body-7;
      color: $romance;
      .circle {
         --size: 5px;
         background-color: $romance;
      }
   }
}
